  }
}
@media (max-width: 767px) {
  .video {
    width: 320px;
  }
}

.video img {
  position: absolute;
  top: 0;
  left: 0;
}

.video button {
  cursor: pointer;
  border: none;
  outline: none;
  position: absolute;
  z-index: 10;
  top: 35%;
  left: 40%;
  transform: translate(-50%, -50%);
  width: 72px;
  height: 72px;
  border-radius: 50%;
  background: linear-gradient(44.89deg, #003D85 25.38%, #B80027 74.89%);
  transform: scale(1);
  transition-property: transform;
  transition-duration: 1s;
}
@media (max-width: 1023px) {
  .video button {
    width: 52px;
    height: 52px;
  }
}
@media (max-width: 767px) {
  .video button {
    width: 50px;
    height: 50px;
    left: 42.5%;
  }
}
.video button::after {
  content: "";
  position: absolute;
  top: 35%;
  left: 45%;
  display: block;
  width: 12px;
  height: 17px;
  border: 8px solid transparent;
  border-left: 15px solid #ffffff;
}
@media (max-width: 1023px) {
  .video button::after {
    left: 42%;
    width: 4px;
    height: 4px;
    border: 7px solid transparent;
    border-left: 12px solid #fff;
  }
}
.video button::before {
  content: "";
  position: absolute;
  top: -20%;
  left: -21%;
  display: block;
  width: 100px;
  height: 100px;
  background-image: url("../img/play.svg");
  background-position: center;
  background-size: contain;
}
@media (max-width: 1023px) {
  .video button::before {
    width: 73px;
    height: 73px;
  }
}
@media (max-width: 767px) {
  .video button::before {
    width: 70px;
    height: 70px;
  }
}
.video button:focus-visible {
  box-shadow: 0 0 0 2px #000000;
}
.video button:hover {
  transform: scale(1.1);
}

.video img {
  position: absolute;
  width: 100%;
  height: 100%;
  border: none;
  margin-top: 0;
}

.video__preview {
  height: 100%;
}
.video__preview::after {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  height: 228px;
  background: rgba(20, 54, 122, 0.5);
}
@media (max-width: 1023px) {
  .video__preview::after {
    height: 167px;
  }
}

